.black-pixel {
  animation: fade-in 0.5s linear forwards;
  content: "*";
  opacity: 0;
  pointer-events: none;
  position: fixed;
  font-family: menlo;
}

.pixel-button {
  font-family: menlo;

  &.active {
    background-color: var(--black);
    color: var(--white);
  }

  &:hover {
    background-color: var(--black);
    color: var(--white);
    cursor: pointer;
  }
}



@keyframes fade-in {
  to {
    opacity: 1;
  }
}
